import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, useNavigate, useLocation } from 'react-router-dom';
import Home from './components/Home';
import Leaderboard from './components/Leaderboard';
import Friends from './components/Friends';
import BottomNav from './components/BottomNav';
import Preloader from './components/Preloader';
import DailyReward from './components/DailyReward';
import Games from './components/Games';
import QuizyWheel from './components/QuizyWheel';
import GameTimer from './components/GameTimer';
import './App.css';

function App() {
  const [loading, setLoading] = useState(true);
  const [showDailyReward, setShowDailyReward] = useState(false);
  const [balance, setBalance] = useState(null); //   - null
  const [user, setUser] = useState(null); //    
  const [referralCode, setReferralCode] = useState(null); //    
  const [dailyRewardData, setDailyRewardData] = useState(null); //   
  const [hasCheckedDailyReward, setHasCheckedDailyReward] = useState(false); //   

  const navigate = useNavigate();
  const location = useLocation(); //      

  useEffect(() => {
    if (window.Telegram && window.Telegram.WebApp) {
      window.Telegram.WebApp.expand();
      window.Telegram.WebApp.setHeaderColor('#112558');
      window.Telegram.WebApp.setBackgroundColor('#112558');
      window.Telegram.WebApp.disableVerticalSwipes();

      const telegramUser = window.Telegram.WebApp.initDataUnsafe.user;
      const referralParam = window.Telegram.WebApp.initDataUnsafe.start_param; //     start_param

      if (telegramUser) {
        console.log('User data from Telegram:', telegramUser);
        setUser(telegramUser); //    
        setReferralCode(referralParam); //    ( )
        fetchUserData(telegramUser, referralParam); //     Firebase
      } else {
        console.log('No user data from Telegram WebApp');
      }
    } else {
      console.log('Not running in Telegram WebApp');
    }

    //   2 
    const preloaderTimer = setTimeout(() => {
      setLoading(false); //  
    }, 2000);

    return () => clearTimeout(preloaderTimer);
  }, []);

  //      ( )  Firebase
  const fetchUserData = async (user, referralCode) => {
    try {
      const response = await fetch(`https://us-central1-quizy-d6ffb.cloudfunctions.net/getUser?userId=${user.id}`, {
        method: 'GET',
        mode: 'cors', //   CORS
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (response.status === 404) {
        //    ,   
        console.log('User not found, creating new user...');
        await createUser(user, referralCode);
        return; //   ,      
      }

      if (!response.ok) {
        throw new Error('Failed to fetch user data');
      }

      const userData = await response.json();
      setBalance(userData.balance); //      
      console.log('User data fetched successfully:', userData);

      //      
      checkDailyReward(user.id);
    } catch (error) {
      console.error('Error fetching user data:', error);
      setBalance(0); //      0
    }
  };

  //       Firebase
  const createUser = async (user, referralCode) => {
    try {
      const response = await fetch('https://us-central1-quizy-d6ffb.cloudfunctions.net/saveUser', {
        method: 'POST',
        mode: 'cors', //   CORS
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userId: user.id,
          firstName: user.first_name,
          lastName: user.last_name || '',
          username: user.username || '',
          balance: referralCode ? 500 : 0, //    ,  
          referralCode: referralCode || null, //   ,  
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to create user');
      }

      const result = await response.json();
      console.log('User created successfully:', result);

      setBalance(referralCode ? 500 : 0); //    
      checkDailyReward(user.id); //      
    } catch (error) {
      console.error('Error creating user:', error);
      setBalance(0); //      0
    }
  };

  //     
  const checkDailyReward = async (userId) => {
    try {
      const response = await fetch(`https://us-central1-quizy-d6ffb.cloudfunctions.net/handleDailyReward?userId=${userId}`, {
        method: 'GET',
        mode: 'cors', //   CORS
        headers: {
          'Content-Type': 'application/json',
        },
      });

      const rewardData = await response.json();

      if (rewardData.success && rewardData.canClaimReward) {
        //    ,   
        setDailyRewardData(rewardData);
        setShowDailyReward(true);
      } else {
        //     ,   Home
        setShowDailyReward(false);
        if (!hasCheckedDailyReward) {
          navigate('/'); //   Home,    
        }
      }

      // ,   
      setHasCheckedDailyReward(true);
    } catch (error) {
      console.error('Error fetching daily reward data:', error);
      setShowDailyReward(false);
      if (!hasCheckedDailyReward) {
        navigate('/'); //   ,    Home
      }
      setHasCheckedDailyReward(true);
    }
  };

  //       Firebase
  const saveUserToFirebase = async (user, balance) => {
    try {
      const response = await fetch('https://us-central1-quizy-d6ffb.cloudfunctions.net/saveUser', {
        method: 'POST',
        mode: 'cors', //   CORS
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userId: user.id,
          firstName: user.first_name,
          lastName: user.last_name || '',
          username: user.username || '',
          balance: balance, //   
          referralCode, //   
        }),
      });

      const result = await response.json();
      console.log('User data saved successfully:', result);
    } catch (error) {
      console.error('Error saving user data:', error);
    }
  };

  //    
  const updateBalance = async (amount) => {
    setBalance((prevBalance) => {
      const newBalance = prevBalance + amount;
      return newBalance;
    });

    if (user) {
      await saveUserToFirebase(user, balance + amount); //     Firebase
    }
  };

  //        
  const fetchBalance = async () => {
    if (user) {
      await fetchUserData(user); //     
    }
  };

  //     DailyReward  Home
  const handleContinue = async () => {
    try {
      if (dailyRewardData && dailyRewardData.rewardAmount) {
        await updateBalance(dailyRewardData.rewardAmount); //      
      }
      setShowDailyReward(false);
      navigate('/'); //   Home   
    } catch (error) {
      console.error('Error during saving user data or balance update:', error);
    }
  };

  //        ,  
  if (balance === null || loading || !hasCheckedDailyReward) {
    return <Preloader />;
  }

  return (
    <div className="App">
      {showDailyReward ? (
        <DailyReward 
          onContinue={handleContinue} 
          userId={user?.id} 
          rewardAmount={dailyRewardData?.rewardAmount} //   
          currentDay={dailyRewardData?.currentDay} //   
        />
      ) : (
        <>
          <Routes>
            <Route path="/" element={<Home userId={user?.id} balance={balance} updateBalance={updateBalance} fetchBalance={fetchBalance} />} />
            <Route path="/leaderboard" element={<Leaderboard />} />
            <Route path="/games" element={<Games />} />
            <Route path="/friends" element={<Friends />} />
            <Route path="/wheel" element={<QuizyWheel />} />
            <Route path="/game-timer" element={<GameTimer onBack={() => navigate('/games')} />} />
          </Routes>
                    {/*   BottomNav */}
          {location.pathname !== '/game-timer' && <BottomNav />}
        </>
      )}
    </div>
  );
}

export default function AppWithRouter() {
  return (
    <Router>
      <App />
    </Router>
  );
}
